* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  width: 300px;
  background-color: #ff4500;
  flex-direction: row;
  justify-content: space-around;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.field:first-child {
  color: #7300ff;
}

.field:nth-child(2) {
  color: green;
}

.field:nth-child(3) {
  color: orange;
}

.field:last-child {
  color: #fff;
}

.value {
  text-transform: uppercase;
  text-align: center;
  font-size: 36px;
}

.form {
  flex-direction: row;
  display: flex;
}

label {
  text-transform: uppercase;
  flex-direction: column;
  margin-left: 10px;
  display: flex;
}

button {
  height: 25px;
  margin-top: 22px;
  margin-left: 10px;
}

#buttonContainer {
  justify-content: center;
  display: flex;
}

.data-start, .data-stop {
  width: 100px;
  height: 50px;
  border-radius: none;
  text-transform: uppercase;
}

